import React from "react"
import { Link } from "gatsby"
import {
  phpImg,
  javascriptImg,
  cssImg,
  htmlImg,
  bootstrapImg,
  vueImg,
  mysqlImg,
} from "../../../../Icons"

import GenerateIcons from "../../../../Generate/Icons"

const Footer = () => {
  const icons = [
    phpImg,
    javascriptImg,
    cssImg,
    htmlImg,
    bootstrapImg,
    vueImg,
    mysqlImg,
  ]

  return (
    <>
      <div className="links">
        <Link to="/laboratorio">Imágenes proyecto</Link>
      </div>
      <div className="icons">
        <GenerateIcons icons={icons} />
      </div>
    </>
  )
}

export default Footer
